@charset "UTF-8";

@font-face {
  font-family: "uwc-website";
  src: url("fonts/uwc-website.eot");
  src: url("fonts/uwc-website.eot?#iefix") format("embedded-opentype"), url("fonts/uwc-website.woff") format("woff"), url("fonts/uwc-website.ttf") format("truetype"), url("fonts/uwc-website.svg#uwc-website") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]::before {
  font-family: "uwc-website" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]::before,
[class*=" icon-"]::before {
  font-family: "uwc-website" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

