// ==== IMPORTS ==== //

// 1. Dependencies
// 2. Configuration
// 3. Patterns
// 4. Sections

// The order of the imports is crucial since they build on top of each other.
// For example, a form contains buttons, a menu can contain a form, etc.
// Note: `bower_components` and `npm_modules` are already in Sass's path.

// 1. Dependencies //

// Modular scale calculator built into your Sass, from http://www.modularscale.com/.
@import "modular-scale/stylesheets/modular-scale";

// Typi: a sass mixin to make responsive typography easy, from https://github.com/zellwk/typi.
@import "typi/scss/typi";

// Mappy Breakpoints: a breakpoint mixin that uses Sass maps, from https://github.com/zellwk/mappy-breakpoints.
@import "mappy-breakpoints/mappy-breakpoints";

// Normalize: make browsers render things more consistently, from https://necolas.github.io/normalize.css/.
@import "deps/normalize";

// Reset: a reset stylesheet to reduce browser inconsistencies, http://meyerweb.com/eric/tools/css/reset/.
@import "deps/reset";

// A lightbox wrapper for Ken's amazing slick carousel, https://mreq.github.io/slick-lightbox/.
@import "deps/slick";
@import "deps/slick-theme";
@import "deps/slick-lightbox";

// 2. Configuration //

@import "config/layout";
@import "config/color";
@import "config/fonts";
@import "config/type";
@import "config/animation";

// 3. Patterns //

//  Styles required by WordPress core.
@import "patterns/wordpress";
@import "patterns/buttons";
@import "patterns/forms";
@import "patterns/menus";
@import "patterns/overlays";

// 4. Sections //

@import "sections/search";
@import "sections/widgets";
@import "sections/navigation";
@import "sections/header";
@import "sections/section";
@import "sections/article";
@import "sections/category";

.entry-content {
  @import "sections/content";
}

@import "sections/contact";
@import "sections/footer";
