// ==== CATEGORY ==== //

/* Style post content here, that appears on category and archive pages. */

/**
 * 1. Box
 * 2. Content
 */

// 1. Box //

.category-post {
  @include ctnr(golden);

  margin-bottom: vr(1);
  display: flex;
  align-items: stretch;

  @include mappy-query(phone) {
    flex-wrap: wrap;
  }


  @include mappy-query(tablet-desktop) {
    border: 1px solid palette(gray, light);
    background-color: palette(white);
    flex-direction: row-reverse;
  }
}

// 2. Content //

.sticky .featured {
  font-weight: $sans-medium;
  font-size: ms(-2);
  line-height: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.06rem;
  display: inline-block;
  padding: 0.1rem 0.5rem;
  margin-bottom: vr(0.5);
  background: linear-gradient(90deg, palette(purple), palette(pink));
  color: palette(white);
}

.category-text {
  .category-title {
    @include font(header3);

    color: palette(blue);
    margin-bottom: vr(0.5);

    @include mappy-query(phone) {
      margin-top: vr(0.75);
    }
  }

  .category-link {
    @include font(button);

    @include link-arrow(right, pink);

    display: inline-block;
    margin-top: vr(0.5);
  }

  @include mappy-query(tablet-desktop) {
    padding: vr(0.75) vr(1);
  }
}

.category-tags {
  @include font(small);

  color: palette(grey);
  padding-bottom: vr(0.25);
}

.category-image {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

@include mappy-query(phone) {
  .category-image {
    width: 100%;
    height: vr(8);
  }
}


@include mappy-query(tablet-desktop) {
  .category-image {
    flex: 1 1 14rem;
  }

  .category-text {
    flex: 1 1 22rem;
  }
}
